	module.exports = {"data":{"markdownRemark":{"html":"<p>There are two ways to access the properties of an object: the dot operator (<code>.</code>) and bracket notation (<code>[]</code>), similar to an array.</p>\n<p>The dot operator is what you use when you know the name of the property you’re trying to access ahead of time.</p>\n<p>Here is a sample of using the dot operator (<code>.</code>) to read an object property:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>var myObj = {\n  prop1: \"val1\",\n  prop2: \"val2\"\n};\nmyObj.prop1; // val1\nmyObj.prop2; // val2</code></pre>\n      </div>","fields":{"slug":"/certificates/accessing-object-properties-with-the-dot-operator/"},"frontmatter":{"title":"Accessing Object Properties with the Dot Operator"}}},"pathContext":{"slug":"/certificates/accessing-object-properties-with-the-dot-operator/"}}

